import React, { useState } from 'react';
import styled from 'styled-components';
import Background from './assets/frame.png';
import Vector from './assets/vector.png';
import './index.css';

const Body = ({ children, description, homepage }) => {
    return (
        <div className="container content row">
            {homepage ? (
                children
            ) : (
                <div className="col-md-12">
                    <div>
                        <div className="content_desc">
                            <div>
                                Avoid falling victim to{' '}
                                <span className="text_default_color text_bold">scammers' impersonation</span>
                            </div>
                            <p className="text_bold">Be a step ahead of Scammers</p>
                        </div>
                        <div className="content_wrapper">
                            <div className="content_wrapper_box">
                                <div className="content_body">{children}</div>
                                {description && <div className="box_description">{description}</div>}
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default Body;
