import React from 'react';
import styled from 'styled-components';
import { colors } from '@components/typo';

const Container = styled.div`
    background: #fff;
    border-radius: 6px;
    padding: ${(props) => (props.nopadding ? '0px' : '10px')};
    margin-bottom: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
    transition: all 0.3s ease-in-out, background 0s, color 0s, border-color 0s;
    border-top: ${({ top_border }) =>
        top_border ? `5px solid ${colors.defaultColor}` : `0px solid ${colors.defaultColor};`};
    display: flex;
    flex-direction: column;
    position: relative;
`;

const CardHeader = styled.div`
    border-bottom: 1px solid ${colors.grey};
    padding: 10px;
    justify-content: space-between;
    display: flex;
    align-items: center;
`;

const CardTitle = styled.p`
    font-size: 16px;
    font-weight: 700;
    margin: 0px;
`;

const CardFooter = styled.div`
    border-top: 1px solid ${colors.grey};
    padding: 10px;
`;

const CardBody = styled.div`
    padding: 10px;
    flex: 1;
`;

export default function Cards({
    children,
    nopadding,
    top_border,
    cardTitle,
    cardFooter,
    actionButton,
    style,
    ...props
}) {
    return (
        <Container nopadding={nopadding} style={style} top_border={top_border} {...props}>
            {cardTitle ? (
                <CardHeader>
                    <CardTitle>{cardTitle}</CardTitle>
                    {actionButton && actionButton}
                </CardHeader>
            ) : (
                <></>
            )}
            <CardBody>{children}</CardBody>
            {cardFooter ? <CardFooter>{cardFooter}</CardFooter> : <></>}
        </Container>
    );
}
