import React, { useCallback, useMemo, useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAlertState } from '@components/libs/Notify/useAlertState';
import { getHighlightedText } from '../utils';

const CellContainer = ({
    rowId,
    data,
    column,
    rowIndex,
    colIndex,
    isEdit,
    disableSelection,
    isSelected,
    tableManager,
    forwardRef,
}) => {
    let {
        id,
        config: {
            highlightSearch,
            tableHasSelection,
            additionalProps: { cellContainer: additionalProps = {} },
        },
        rowsApi: { onRowClick },
        rowEditApi: { editRow, setEditRow },
        rowSelectionApi: { toggleRowSelection },
        searchApi: { searchText, valuePassesSearch },
        columnsApi: { visibleColumns },
    } = tableManager;
    const alertState = useAlertState();
    const navigate = useNavigate();
    const [modalState, setModalState] = useState(false);
    const [modalState2, setModalState2] = useState(false);
    const [buttonLoading, setButtonLoading] = useState(false);

    const modalStateHandler = (state) => setModalState(state);
    const modalStateHandler2 = (state) => setModalState2(state);
    const buttonLoadingHandler = (state) => setButtonLoading(state);
    const navigateTo = (to) => navigate(to, { replace: false });
    const LinkBox = ({ to, children }) => <Link to={to}>{children}</Link>;

    const getClassNames = () => {
        let classNames;
        const all = `rgt-cell rgt-row-${rowIndex} rgt-row-${rowIndex % 2 === 0 ? 'even' : 'odd'}${
            isSelected ? ' rgt-row-selected' : ''
        }${isEdit ? ' rgt-row-edit' : ''} ${additionalProps.className || ''}`.trim();
        const virtualDefault = `${
            !tableHasSelection ? '' : disableSelection ? ' rgt-row-not-selectable' : ' rgt-row-selectable'
        }`;
        const checkboxDefault = `${column.pinned && colIndex === 0 ? ' rgt-cell-pinned rgt-cell-pinned-left' : ''}${
            column.pinned && colIndex === visibleColumns.length - 1 ? ' rgt-cell-pinned rgt-cell-pinned-right' : ''
        } ${column.className}`.trim();

        switch (column.id) {
            case 'virtual':
                classNames = `${all} rgt-cell-virtual ${virtualDefault}`;
                break;
            case 'checkbox':
                classNames = `${all} rgt-cell-checkbox ${checkboxDefault}`;
                break;
            default:
                classNames = `${all} rgt-cell-${column.field} ${virtualDefault} ${checkboxDefault}`;
        }

        return classNames;
    };

    const getValue = () => {
        let value;

        switch (column.id) {
            case 'checkbox':
                value = isSelected;
                break;
            default:
                value =
                    data &&
                    column
                        .getValue?.({
                            tableManager,
                            value: isEdit ? editRow[column.field] : data[column.field],
                            column,
                        })
                        ?.toString?.();
                if (!isEdit && highlightSearch && valuePassesSearch(value, column))
                    return getHighlightedText(value, searchText);
        }

        return value;
    };

    const onMouseOver = useCallback(
        (event) => {
            document
                .querySelectorAll(`#${id} .rgt-row-${rowIndex}`)
                .forEach((cell) => cell.classList.add('rgt-row-hover'));
            additionalProps.onMouseOver?.(event);
        },
        [id, rowIndex, additionalProps],
    );

    const onMouseOut = useCallback(
        (event) => {
            document
                .querySelectorAll(`#${id} .rgt-row-${rowIndex}`)
                .forEach((cell) => cell.classList.remove('rgt-row-hover'));
            additionalProps.onMouseOut?.(event);
        },
        [id, rowIndex, additionalProps],
    );

    if (data && onRowClick) {
        additionalProps = {
            onClick: (event) => onRowClick({ rowIndex, data, column, isEdit, event }, tableManager),
            ...additionalProps,
        };
    }

    let classNames = getClassNames();
    let value = getValue();

    let cellProps = {
        tableManager,
        value,
        data,
        column,
        colIndex,
        rowIndex,
        modalState,
        modalStateHandler,
        modalStateHandler2,
        modalState2,
        navigateTo,
        LinkBox,
        buttonLoadingHandler,
        buttonLoading,
        alertState
    };

    const isFirstEditableCell = useMemo(
        () =>
            visibleColumns.findIndex(
                (visibleColumn) => visibleColumn.id !== 'checkbox' && visibleColumn.editable !== false,
            ) === colIndex,
        [visibleColumns, colIndex],
    );

    return (
        <div
            data-row-id={rowId.toString()}
            data-row-index={rowIndex.toString()}
            data-column-id={column.id.toString()}
            {...additionalProps}
            onMouseOver={onMouseOver}
            onMouseOut={onMouseOut}
            className={classNames}
            ref={forwardRef}
        >
            {column.id === 'virtual'
                ? null
                : column.id === 'checkbox'
                ? column.cellRenderer({
                      ...cellProps,
                      onChange: () => toggleRowSelection(rowId),
                      disabled: disableSelection,
                  })
                : !data
                ? column.placeHolderRenderer(cellProps)
                : column.editable && isEdit
                ? column.editorCellRenderer({
                      ...cellProps,
                      onChange: setEditRow,
                      isFirstEditableCell,
                  })
                : column.cellRenderer(cellProps)}
        </div>
    );
};

export default CellContainer;
