import React from 'react';
import styled from 'styled-components';
import { RiDeleteBinLine, RiFileCopyLine } from 'react-icons/ri';
import {AiOutlineEdit, AiOutlineEye } from 'react-icons/ai';

const Button = styled.button`
    background-color: rgba(251, 164, 11, 0.18);
    outline: none;
    cursor: pointer;
    margin-right: 10px;
    padding: 2px;
    display: inline-flex;
    border: 1px solid #FCFEFE;
    color: #FCFEFE;
`;

export const DeleteButton = (props) => {
    return (
        <Button {...props}>
            <RiDeleteBinLine color="#ff9b04" size={18} />
        </Button>
    );
};


export const EditButton = (props) => {
    return (
        <Button {...props}>
            <AiOutlineEdit color="#ff9b04" size={18} />
        </Button>
    );
};

export const ViewButton = (props) => {
    return (
        <Button {...props}>
            <AiOutlineEye color="#ff9b04" size={18} />
        </Button>
    );
};


