import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Logo from '@components/assets/Logo/variate_1.png';
import { FaUserCircle } from 'react-icons/fa';
import Modal from '@modules/Modal';
import { colors } from '@components/typo';
import SojiBaseInit from '@modules/SojiBase';
import useUserDetails from '@components/Hooks/useUserDetails';
import PreviousSearches from '../../PreviousSearches';
import './index.css';

const Header = () => {
    const useUserDetail = useUserDetails();

    const [state, setState] = useState({
        onPreviousSearchesModal: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const logout = async () => {
        const SOJIBase = await SojiBaseInit;
        await SOJIBase.deleteCookie();
        useUserDetail.set(false)
    }

    return (
        <>
            <div className="main_header">
                <div className="main_header_wrapper container">
                    <div className="logo_container">
                        <img src={Logo} />
                    </div>
                    <div className="nav_bar_container">
                        <Link to="/">
                            <div className="nav_dot"></div> Home
                        </Link>
                        <a href="http://company.sojiare.com">
                            <div className="nav_dot"></div>Company
                        </a>
                        <Link to="/">
                            <div className="nav_dot"></div>Api
                        </Link>
                        <Link to="/pricing">
                            <div className="nav_dot"></div>Pricing
                        </Link>
                        <Link to="/">
                            <div className="nav_dot"></div>How it works
                        </Link>
                        <Link to="/">
                            <div className="nav_dot"></div>About us
                        </Link>
                    </div>

                    {useUserDetail.get() ? (
                        <label className="dropdown">
                            <div className="dd-button">
                                <div className="action_container">
                                    <FaUserCircle size={30} color={colors.textColor} />
                                    <div className="username">{useUserDetail.get().lastName}</div>
                                </div>
                            </div>
                            <input type="checkbox" className="dd-input" id="test" />
                            <ul className="dd-menu">
                                <li onClick={() => stateHandler('onPreviousSearchesModal', true)}>Previous Searches</li>
                                <li>Upgrade Account</li>
                                <li className="divider"></li>
                                <li onClick={() => logout()}>Sign Out</li>
                            </ul>
                        </label>
                    ) : (
                        <div className="action_container">
                            <a href="/authe/login">Login</a>
                            <a href="/authe/signup" className="sign_up">
                                Sign Up
                            </a>
                        </div>
                    )}
                </div>
            </div>
            <Modal
                visible={state.onPreviousSearchesModal}
                onRequestClose={() => stateHandler('onPreviousSearchesModal', false)}
            >
                <PreviousSearches modal onRequestClose={() => stateHandler('onPreviousSearchesModal', false)} state={state} />
            </Modal>
        </>
    );
};

export default Header;
