import React from 'react';

import Header from './Header';
import Body from './Body';
import Footer from './Footer';

const LandingPageStructure = ({ children, description,homepage, fullpage }) => {
    return (
        <div className="main_container">
            <Header />
            {fullpage ? children : <Body description={description} homepage={homepage}>{children}</Body>}
            {/* <Footer /> */}
        </div>
    );
};

export default LandingPageStructure;
