import React, { useState } from 'react';
import styled from 'styled-components';
import { IoArrowBackSharp } from 'react-icons/io5';
import { colors } from '@components/typo';

const Container = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
`;

const ActionButtonContainer = styled.div`
    flex: 1;
    justify-content: flex-end;
    display: flex;
`;

const BackButton = styled.div`
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
`;

const Header = ({ tableManager, searchByDate, ...props }) => {
    const [dateRange, setDateRange] = useState([null, null]);
    const [startDate, endDate] = dateRange;

    const {
        config: {
            showColumnVisibilityManager,
            components: { ColumnVisibility, Search },
            additionalProps: { header: additionalProps = {} },
            showSearch,
        },
        columnsApi: { columns },
        columnsVisibilityApi: { toggleColumnVisibility },
        searchApi: { setSearchText, searchText },
    } = tableManager;

    const classNames = ('rgt-header-container ' + (additionalProps.className || '')).trim();

    return (
        <>
            <Container>
                {props.backButton && (
                    <BackButton onClick={() => props.backButton()}>
                        <IoArrowBackSharp size={30} color={colors.textColor} />
                    </BackButton>
                )}

                <div {...additionalProps} className={classNames}>
                    {showSearch !== false ? (
                        <Search value={searchText} onChange={setSearchText} tableManager={tableManager} />
                    ) : (
                        <span></span>
                    )}
                </div>
                <ActionButtonContainer>
                    {/* <Action data={props.allData} {...props} /> */}
                    {showColumnVisibilityManager !== false ? (
                        <ColumnVisibility
                            columns={columns}
                            onChange={toggleColumnVisibility}
                            tableManager={tableManager}
                        />
                    ) : (
                        <span></span>
                    )}
                </ActionButtonContainer>
            </Container>
        </>
    );
};

export default Header;
