import React from 'react';
import styled from 'styled-components';
import { ActivityIndicator } from 'react-native';

import Card from '@components/libs/Card';
import { colors } from '@components/typo';

const Container = styled(Card)`
    height: 300px
    width: 300px;
    background-color: #fff;
    border-radius: 10px;
    padding: 0 !important;
`;

const Wrapper = styled.div`
    height: 100%;
    width: 100%;
`;

const ActionButtonContainer = styled.div`
    display: flex;
    flex-direction: row;
    margin-top: 15px;
    justify-content: flex-end;
`;

const Button = styled.button`
    background-color: ${({ background }) => background};
    border: 1px solid rgb(255, 255, 255);
    color: #fff;
    border-radius: 6px;
    padding: 0px 12px;
    display: block;
    min-height: 24px;
    max-height: 24px;
    min-width: 60px;
    outline: none;
    position: relative;
    font-size: 12px;
    margin-right: 10px;
    rgba(0, 0, 0, 0.25) 1px 1px 1px 0px;
    font-weight: 600;
`;

export default function Alert({ actionDescription,backgroundColor, onRequestClose, onActionButtonClick, buttonLoading, actionButtonText }) {
    return (
        <Container cardTitle="Notification" nopadding>
            <Wrapper>
                {actionDescription}
                <ActionButtonContainer>
                    <Button background="rgb(197, 197, 197)" onClick={() => onRequestClose()}>
                        No, Close
                    </Button>
                    <Button background={backgroundColor ? backgroundColor : colors.defaultColor} onClick={() => onActionButtonClick()}>
                        {buttonLoading ? <ActivityIndicator color="#fff" /> : actionButtonText ? actionButtonText : 'Okay, Approve'}
                    </Button>
                </ActionButtonContainer>
            </Wrapper>
        </Container>
    );
}
