import React from 'react';
import styled from 'styled-components';
import Alert from '@components/libs/Alert';
// import MevronBaseInit from '@modules/SOJIBase';
import Modal from '@modules/Modal';
import { DeleteButton, EditButton } from '@modules/DataTable/Buttons';


const Container = styled.div`
    height: 100%;
    width: 100%;
    display: inline-flex;
    padding: 0 20px;
    justify-content: flex-end;
    align-items: center;
`;

const actionButton = ({
    data,
    modalState,
    modalStateHandler,
    modalState2,
    modalStateHandler2,
    buttonLoadingHandler,
    buttonLoading,
    alertState,
}) => {


    return (
        <>
            <Container>
                <DeleteButton onClick={() => modalStateHandler(true)} />
                <EditButton onClick={() => modalStateHandler2(true)} />
            </Container>
            <Alert
                visible={modalState}
                setModal={() => modalStateHandler(false)}
                actionDescription={`Are you sure you want to delete promo code (${data.promoCode})?`}
                // onActionButtonClick={() => onActionButtonClick()}
                buttonLoading={buttonLoading}
                actionButtonText="Okay, Delete"
            />
            <Modal visible={modalState2} onRequestClose={() => modalStateHandler2(false)}>
                
            </Modal>
        </>
    );
};
export default actionButton;
