import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
    position: relative;
    padding: 0 20px;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    display: flex;
`;

const Text = styled.span`
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
`;

const RowCell = ({ value,data, ...props }) => {
    return (
        <Container>
            <Text title={value}>{value}</Text>
        </Container>
    );
};

export default RowCell;
