import React, { useState, useRef } from 'react';
import GridTable from './GridTable';


const DataTable = ({ getColumns, MOCK_DATA, ...props }) => {
    const tableManager = useRef(null);
    const setTableManager = (tm) => (tableManager.current = tm);
    const [data] = useState(MOCK_DATA)
    const [rows, setRows] = useState([]);
    const rowsRef = useRef([]);
    const [totalRows, setTotalRows] = useState(null);
    const [editRowId, setEditRowId] = useState(null);
    const [searchText, setSearchText] = useState('');
    const [selectedRowsIds, setSelectedRowsIds] = useState([]);
    const [sort, setSort] = useState({ colId: null, isAsc: true });
    const [page, setPage] = useState(1);
    const [pageSize, setPageSize] = useState(20);
    const [pageSizes, setPageSizes] = useState([20, 50, 100]);
    const [enableColumnsReorder, setEnableColumnsReorder] = useState(true);
    const [highlightSearch, setHighlightSearch] = useState(true);
    const [showSearch, setShowSearch] = useState(true);
    const [showRowsInformation, setShowRowsInformation] = useState(true);
    const [showColumnVisibilityManager, setShowColumnVisibilityManager] = useState(true);
    const [isHeaderSticky, setIsHeaderSticky] = useState(true);
    const [isVirtualScroll, setIsVirtualScroll] = useState(true);
    const [isPaginated, setIsPaginated] = useState(true);
    const [minSearchChars, setMinSearchChars] = useState(2);
    const [minColumnResizeWidth, setMinColumnWidth] = useState(70);
    const [columns, setColumns] = useState(
        getColumns({
            setRowsData: (newRows) => {
                rowsRef.current = newRows;
                setRows(rowsRef.current);
            },
        }),
    );

    const onRowsRequest = async (requestData, tableManager) => {
        const {
            sortApi: { sortRows },
            searchApi: { searchRows },
            asyncApi: { mergeRowsAt },
        } = tableManager;

        let allRows = data;
        allRows = searchRows(allRows);
        allRows = sortRows(allRows);

        await new Promise((r) => setTimeout(r, 1300));

        rowsRef.current = [
            ...mergeRowsAt(rowsRef.current, allRows.slice(requestData.from, requestData.to), requestData.from),
        ];
        setRows(rowsRef.current);
        setTotalRows(allRows.length);
    };

    const onRowsReset = () => {
        rowsRef.current = [];
        setRows(rowsRef.current);
        setTotalRows(null);
    };

    const a = (
        <GridTable
            rows={rows}
            columns={columns}
            onColumnsChange={setColumns}
            onRowsRequest={onRowsRequest}
            totalRows={totalRows}
            onRowsReset={onRowsReset}
            editRowId={editRowId}
            onEditRowIdChange={setEditRowId}
            selectedRowsIds={selectedRowsIds}
            onSelectedRowsChange={setSelectedRowsIds}
            onLoad={setTableManager}
            searchText={searchText}
            onSearchTextChange={setSearchText}
            sort={sort}
            onSortChange={setSort}
            page={page}
            onPageChange={setPage}
            pageSize={pageSize}
            onPageSizeChange={setPageSize}
            pageSizes={pageSizes}
            enableColumnsReorder={enableColumnsReorder}
            highlightSearch={highlightSearch}
            showSearch={showSearch}
            showRowsInformation={showRowsInformation}
            showColumnVisibilityManager={showColumnVisibilityManager}
            isHeaderSticky={isHeaderSticky}
            isVirtualScroll={isVirtualScroll}
            isPaginated={isPaginated}
            minSearchChars={minSearchChars}
            minColumnResizeWidth={minColumnResizeWidth}
            allData={data}
            {...props}
        />
    );

    return a;
};

export default DataTable;
