import React, { useState, useEffect } from 'react';
import SojiBaseInit from '@modules/SojiBase';
import PagePreloader from '@components/preloader/PagePreloader';
import DataTable from '@modules/DataTable';
import getColumns from './Table/Column';
import styled from 'styled-components';

const Container = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .modal_container {
        width: 60%;
    }

    @media only screen and (max-width: 767px) {
        .modal_container {
            width: 100%;
        }
    }
`;

export default function PreviousSearches({ onRequestClose }) {
    const [state, setState] = useState({
        fetchedData: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get('v1/suspected_users/get-all_recent_documents');
            getter.on('static', async (response) => {
                const data = response.response.data;
                stateHandler('fetchedData', data);
            });
        })();
    }, []);

    return (
        <Container>
            <div className="modal_container">
                {state.fetchedData ? (
                    <DataTable
                        getColumns={getColumns}
                        MOCK_DATA={[]}
                        texts="No Results fo"
                        backButton={onRequestClose}
                    />
                ) : (
                    <PagePreloader />
                )}
            </div>
        </Container>
    );
}
